import React from 'react'
import { connect } from "react-redux"
import Header from './parts/Header/'
import Footer from './parts/Footer/'
import { f_ifexist } from "helpers/formatter"
import { cookiesInit } from "helpers/cookies"


import {  setCookie } from "helpers/cookies"



// DECORATOR
// ES: https://benmccormick.org/2019/02/11/reusable-react

const withTemplate = WrappedComponent => {
   class ComponentWithCounter extends React.Component {
     componentDidMount() {
       // porto in alto lo scroll ad ogni cambio pagina
       window.scrollTo(0, 0);
       cookiesInit(this.props)
     }
     componentWillReceiveProps(nextProps) {
       // nel momento in cui lo stato viene aggiornato,
       // vai ad aggiornare anche il cookie
       setCookie('cart', nextProps.cart )
       setCookie('user', nextProps.user )
     }
     render() {
       const { fullwidth, className } = this.props
         return (
           <div>
             <section className={"t-template "+( !!fullwidth ? ' t-template--fullwidth' : '' )+f_ifexist(className)}>
               <Header />
               {!fullwidth}
               <div className={"main-container "+( !fullwidth ? 'container px-3 pt-4 pb-3' : 'px-0' )}>
                 {/*this.props.children*/}
                 <WrappedComponent {...this.props} {...this.callbacks} />
               </div>
               <hr className="mt-3" />
               <Footer />
             </section>
          </div>
         )
     }
  }
  //return ComponentWithCounter;
  const mapStateToProps = (state) => {
    const { cart, user } = state
    return {
      cart, user
    }
  }
  return connect(mapStateToProps)( ComponentWithCounter );

};

export default withTemplate
