import React from 'react'
import ReactHtmlParser from 'react-html-parser';
import ProdLink from "pages/Prods/Prod/parts/Link"
import Schema from "pages/Prods/Prod/parts/Schema"
import { Price, OldPrice } from "components/Prices"
import ProdImage from 'pages/Prods/Prod/parts/Image/index.js'

const Mini = (props) => {
    const { prod, className } = props
    let className_f = className ? className : ''
    return (
      <Schema prod={prod} className={"c-row row mb-2 "+className_f}>
        <div className="col-3 pr-0 pb-1 ">
          <ProdImage prod={prod} size={'thumb'} useBkg={true} wrapperStyle={"c-image--bordered"} />
        </div>
        <div className="col">
          <p className="p-0 m-0"><strong>{prod.name}</strong> <br /> <Price prod={prod} /></p>
        </div>
        {props.children && <div className="col-3 text-right">{props.children}</div>}
      </Schema>
    )
}

const Full = (props) => {
    const { prod, className } = props
    let className_f = className ? className : ''
    let prices = <div className="pt-2 pb-1">
                  <strong className="text-body h4 text-nowrap"> <Price prod={prod} /> </strong>
                  <s className="text-muted text-nowrap"> <OldPrice prod={prod}  /> </s>
                 </div>
    return (
      <Schema prod={prod} className={"c-row row "+className_f}>
        <div className='col-12'>
          <div className='row'>
            <div className='col-4 col-sm-3 col-md-3 col-lg-2 mb-3 mb-sm-0 mx-auto pr-0'>
              <ProdImage prod={prod} size={'thumb'} useBkg={true} wrapperStyle={"c-image--bordered"} useLink={true} />
            </div>
            <div className='col'>
              <ProdLink prod={prod}>
                <span className="h4 text-success d-block d-sm-none text-break" >{prod.name}</span>
                <span className="h3 text-success d-none d-sm-block text-break" >{prod.name}</span>
              </ProdLink>
              <div className="d-none d-md-block">
                {prices}
                <p className="my-2 text-break h5">{ReactHtmlParser(prod.claim)}</p>
                <div className='card-text text-break'>{ReactHtmlParser(prod.description)}</div>
              </div>
              <div className="d-block d-md-none">
                {prices}
                <p className="my-2 text-break">{ReactHtmlParser(prod.claim)}</p>
              </div>
            </div>
            {props.children && <div className="col-2 col-md-3 text-right">{props.children}</div>}
          </div>
          <hr />
        </div>
      </Schema>
    )
}

export { Full, Mini }
