import React, { Component } from 'react'
import { f_cur, f_ifexist } from "helpers/formatter"
import {i18nContext} from 'contexts/i18n';

export class Price extends Component {
  render(){
    const { prod } = this.props
    let currency = f_cur(prod.currency);
    return (
      <React.Fragment>
        <span>{prod.price}</span>
        <span>{currency}</span>
      </React.Fragment>
    )
  }
}

export class OldPrice extends Component {
  render(){
    const { prod } = this.props
    let currency = f_cur(prod.currency);
    return (
      <React.Fragment>
        <span>{prod.oldprice}</span>
        <span>{currency}</span>
      </React.Fragment>
    )
  }
}

export default class PricesBox extends Component {
  static contextType = i18nContext;
  render() {
    let i18n = this.context;
    const { prod, priceStyle, oldPriceStyle, pricesStyle, colLabelStyle, colPriceStyle } = this.props
    const pricesStyle_f = f_ifexist(pricesStyle,'col-12 text-left')
    const colLabelStyle_f = f_ifexist(colLabelStyle,'col col-md-6 col-lg-4')
    const colPriceStyle_f = f_ifexist(colPriceStyle,'col')
    const priceStyle_f = f_ifexist(priceStyle,'h3')
    const oldPriceStyle_f = f_ifexist(oldPriceStyle)
    return (
      <div className="c-price">
        <div className="row">
          <div className={pricesStyle_f}>
            <div className="row align-items-end text-muted">
              <div className={colLabelStyle_f}>
                {i18n.base_price}
              </div>
              <div className={colPriceStyle_f}>
                <span className={"text-nowrap prod-oldprice "+oldPriceStyle_f}>
                  <del><OldPrice prod={prod} /></del>
                </span>
              </div>
            </div>
          </div>
          <div className={pricesStyle_f}>
            <div className="row mt-lg-2 align-items-end text-success">
              <div className={colLabelStyle_f}>
                <strong>
                  {i18n.best_price}
                </strong>
              </div>
              <div className={colPriceStyle_f}>
                <span className={"text-nowrap prod-price "+priceStyle_f+" text-success"}>
                  <strong><Price prod={prod} /></strong>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
PricesBox.contextType = i18nContext
