import './style.scss';
import React, { Component } from 'react'
import ReactHtmlParser from 'react-html-parser';
import PricesBox from 'components/Prices'
import ProdImage from 'pages/Prods/Prod/parts/Image/index.js'
import Actions from 'pages/Prods/Prod/parts/Actions/index.js'
import TrustabilityBadges from "components/TrustabilityBadges"

export default  class Infos extends Component {
  render(){
    const { prod, openModalHandler, addToCart, modalInDesktop, isSticked } = this.props
    const modalInDesktopStyle = (!modalInDesktop ? ' d-lg-none ' : '')
    const cardStyle = " card w-100 mw-100 bkg--dark "
    return (
      <React.Fragment>
          <div className="row c-info">
              {/* titolo mobile */}
              <div className="col-12 d-block d-sm-none">
                <h1 className="text-break m-0 p-0">{prod.name}</h1>
                <h5 className="mt-3 mb-4">{ReactHtmlParser(prod.claim)}</h5>
              </div>
              <div className="col-12 col-sm-5">
                <ProdImage prod={prod} useLink={true} useBkg={true} wrapperStyle={"c-image--bordered"} notLazy={true} />
              </div>
              <div className="col-12 col-sm-7">
                  {/* titolo tablet e desktop */}
                  <div className="d-none d-sm-block">
                    <h1>{prod.name}</h1>
                    <h5>{ReactHtmlParser(prod.claim)}</h5>
                    <hr />
                  </div>
                  <div className="d-none d-sm-block"> {ReactHtmlParser(prod.description)} </div>
              </div>
          </div>
          <div className={cardStyle+" card--no-border-bottom mt-3 px-3 pt-3 pb-0 "+modalInDesktopStyle}>
              <div className="row">
              <div className="col-12 px-3 col-sm-9 px-sm-0 col-md-7 mx-auto">
                <PricesBox prod={prod} colLabelStyle="col-7 col-sm-5 text-left" colPriceStyle="col x-text-right" />
              </div>
              </div>
          </div>
          <div className={cardStyle+" card--no-border-top mb-3 px-3 pb-3 pt-0 "+modalInDesktopStyle+" "+( (isSticked) ? " sticky-top sticky-top--sticked " : '' )}>
            <div className="row">
              <div className="col-12 col-sm-10 col-md-8 mx-auto">
              <Actions openModalHandler={openModalHandler} addToCart={addToCart} prod={prod} />
             </div>
           </div>
         </div>
          <div className="d-block d-lg-none" >
            <TrustabilityBadges itemStyle={'is-icons-empty'} />
            <hr />
          </div>
          <div className="mt-3 d-block d-sm-none"> {ReactHtmlParser(prod.description)} </div>
      </React.Fragment>
    )
  }
}
