import './style.scss';
import React, { Component } from 'react'
import Image from 'components/Image'
import { getProdLink, getProdLinkImg } from "helpers/getPath"
import {i18nContext} from 'contexts/i18n';
import { f_ifexist } from "helpers/formatter"

export default class ProdImage extends Component {
  static contextType = i18nContext;
  render(){
    let i18n = this.context;
    const { prod, size, imgStyle, useLink, wrapperStyle, containerStyle, imgStyleExternal, notLazy } = this.props
    const sizeName = size ? '_'+size : ''
    let prodImg = getProdLinkImg(i18n.lang,prod)+'/prod'+sizeName+'.png'
    let prodLink = useLink ? getProdLink(i18n.lang,prod) : false
    return(
      <Image src={prodImg}
             alt={prod.name}
             link={prodLink}
             imgStyle={'c-prod-image'+f_ifexist(imgStyle)}
             imgStyleExternal={'c-prod-image__external'+f_ifexist(imgStyleExternal)}
             wrapperStyle={'c-prod-image__wrapper'+f_ifexist(wrapperStyle)}
             containerStyle={'c-image--squared-centered c-prod-image__container'+f_ifexist(containerStyle)}
             notLazy={notLazy} />
    )
  }
}
ProdImage.contextType = i18nContext
