import React, { Component } from 'react'
import { connect } from "react-redux"
import List from './List'
import MenuIcon from "templates/parts/Header/MenuIcon.js"

class Header extends Component {
  toggleDropdown = () => { this.props.statusMenu('toggle') }
  render() {
    const { showMenu, statusMenu } = this.props
    return (
      <React.Fragment>
        <div className="nav-item dropdown">
          <MenuIcon icon={'icon-bars'}
                    toggleDropdown={this.toggleDropdown}
                    showMenu={showMenu} />
          <List
            listClass={"dropdown-menu "+( showMenu ? 'show' : '' )}
            elemsClass="nav-item"
            linksClass="nav-link"
            statusNavigation={statusMenu}
          />
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  const { cart } = state
  return {
    cart
  }
}
export default connect(mapStateToProps)( Header );
