// modifiche e controlli su oggetti e jsons

export function parseIfJsonString(value) {
    return IsJsonString(value) ? JSON.parse( value ) : false
}

export function IsJsonString(str) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

export function objIsEmpty(o) {
  let obj = parseIfJsonString(o) //se ho una stringa json la converto se no dà false.
  if( typeof obj !== 'object' ) {
    console.error( 'objIsEmpty: '+obj+' is not an object but '+(typeof obj) );
    return false
  }
  for(var prop in obj) {
    if(obj.hasOwnProperty(prop)) {
      return false;
    }
  }
  return JSON.stringify(obj) === JSON.stringify({}) || JSON.stringify(obj) === undefined
}

export function indexProdInList(prod,list) {
  let prodExist = -1; // metto a -1 perchè per me l'indice a zero è un valore true
  if( prod && prod.id && list && list.length > 0 ) {
    // ciclo sulla lista prodotti  nello stato e controllo se c'è il prod corrente
    for (let i = 0; i < list.length; i++) {
      if( list[i].id === prod.id ) {
        prodExist = i
      }
    }
  }
  // quindi se è false restituisce -1, se true l'indice del prodotto.
  return prodExist;
}
